.movieCard {
  border-radius: 4px;
  background-color: #fff;
  overflow: hidden;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14),
    0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.movieContent {
  padding: 8px;
}

.movieImg {
  display: block;
  max-width: 100%;
}

.movieTitle {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 500;
}

.movieDescr {
  margin-bottom: 0;
  line-height: 1.5;
}
